.site-list {
  font-weight: bold;

  z-index: 2;
}


.site-list a {
  font-size: 18px;
  line-height: 32px;

  display: block;

  padding: 15px 20px;
  padding-bottom: 16px;

  text-align: center;
  text-decoration: none;
  text-transform: uppercase;

  color: #ffffff;
  border-top: 1px solid rgb(72, 84, 97);
  border-bottom: 1px solid rgb(0, 0, 1);
  background-color: @bg-lightblue;
}


.site-list__item:nth-of-type(1) a {
  padding: 7px 20px;

  text-align: left;
}

@media (min-width: @tablet-width) {
  .site-list a {
    padding: 15px 20px;
    padding-bottom: 14px;
  }

  .site-list__item:nth-of-type(1) a {
    height: 108px;
    padding: 0;
  }
}


@media (min-width: @desktop-width) {
  .site-list {
    font-weight: normal;

    display: flex;

    width: 960px;
    margin: 0 auto;
    padding: 0;

    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
  }

  .site-list a {
    font-size: 18px;
    line-height: 18px;

    margin-right: 82px;
    padding: 0;

    text-transform: none;

    border: none;
    background-color: inherit;
  }

  .site-list a[href] {
    &:hover {
      color: @pink-hover;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
    }
  }

  .site-list__item:nth-of-type(1) a {
    margin-right: 170px;
    margin-left: 10px;
  }

  .site-list__item:nth-of-type(4) a {
    margin-right: 76px;
  }

  .site-list__item:nth-of-type(5) a {
    margin-right: 0;
  }

  .site-list__item--active a {
    position: relative;

    &::after {
      position: absolute;
      bottom: -15px;
      left: 0;

      width: 100%;
      height: 2px;

      content: "";

      background-color: @bg-white;
    }
  }

  .site-list__item {
    &:nth-of-type(1n+6) a {
      margin-bottom: 35px;
    }
  }
}
