.contacts {
  z-index: 2;

  display: flex;
  flex-direction: column;

  width: 100%;
  min-width: 320px;
  padding-top: 42px;

  background-color: @bg-white;

  align-items: center;
}

.contacts__wrapper {
  z-index: 2;

  display: flex;
  flex-direction: column;

  width: 320px;

  background-color: @bg-white;

  align-items: center;
}

.contacts__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;

  width: 280px;

  text-align: center;

  color: @bg-lightblue;
}

.contacts__text {
  font-size: 18px;
  font-weight: bold;

  display: flex;
  flex-direction: column;

  width: 280px;
  margin: 0;
  margin-top: 32px;

  text-align: center;
}

.contacts__text--email {
  margin-top: 20px;
  padding-left: 5px;
}

.contacts__text--adress {
  margin-top: 23px;
}

.contacts__link {
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;

  color: @pink;
}

.contacts__text:nth-of-type(3) {
  padding-bottom: 36px;
}

.contacts__text--email .contacts__link {
  cursor: pointer;
  text-decoration: none;

  &:hover {
    color: @bg-lightblue;
  }

  &:active,
  &:focus {
    color: rgba(40, 54, 69, 0.3);
  }
}

.contacts__map {
  overflow: hidden;
  position: relative;
  width: 100%;
  min-width: 320px;
  height: 200px;
  background-image: url("../img/bg-map-mobile@1x.png");
  background-color: @bg-grey;
  background-repeat: no-repeat;
  background-position: center -445px;
  background-size: 320px auto;

  &::after {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 33px;

    content: "";
    background-image: url("../img/bg-triangle-white-footer.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 33px;
  }

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.jpg");
  }
}

.contacts__map-area {
  width: 100%;
  height: 100%;
  border: none;
}

.contacts__map-inter {
  height: 200px;
}

@media (min-width: @tablet-width) {
  .contacts {
    padding-top: 46px;
  }

  .contacts__wrapper {
    flex-direction: row;

    width: 660px;

    flex-wrap: wrap;
    justify-content: center;
  }

  .contacts__title {
    font-size: 40px;
    line-height: 48px;

    width: 500px;
  }

  .contacts__text {
    width: 315px;
    margin-top: 42px;
  }

  .contacts__text:nth-of-type(3) {
    margin-top: 44px;
    padding-bottom: 27px;
  }

  .contacts__text--adress {
    width: 280px;
  }

  .contacts__link {
    margin-top: 3px;
  }

  .contacts__map {
    position: relative;

    min-width: 660px;
    height: 373px;

    background-image: url("../img/bg-map-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: 48% 73%;
    background-size: 107% auto;

    &::after {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 68px;

      content: "";

      background-image: url("../img/bg-triangle-white-footer.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 68px;
    }

    &::before {
      top: 193px;
      left: 312px;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.jpg");
    }
  }

  .contacts__map-inter {
    height: 373px;
  }
}


@media (min-width: @desktop-width) {
  .contacts {
    margin-bottom: -29px;
    padding-top: 65px;
    padding-bottom: 0;
  }

  .contacts__wrapper {
    width: 960px;

    justify-content: space-between;
    align-items: flex-start;
  }

  .contacts__title {
    font-size: 50px;
    line-height: 59px;

    width: 700px;
    margin: 0 auto;
  }

  .contacts__text--telefon {
    order: 1;
  }

  .contacts__text {
    width: 315px;
    margin-top: 55px;
  }

  .contacts__text:nth-of-type(3) {
    margin-top: 55px;
    padding-bottom: 26px;

    order: 2;
  }

  .contacts__text--email {
    order: 3;
  }

  .contacts__link {
    margin-top: 20px;
  }

  .contacts__text--adress {
    display: flex;
    flex-direction: column;

    align-items: center;
  }

  .contacts__text--adress .contacts__link {
    width: 220px;
  }

  .contacts__map {
    position: relative;

    width: 100%;
    min-width: 960px;
    height: 495px;

    background-image: url("../img/bg-map-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: 48% 59%;
    background-size: 100% auto;

    &::after {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 100px;

      content: "";

      background-image: url("../img/bg-triangle-white-footer.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 100px;
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.jpg");
    }
  }
}
